.loader{
  display: flex;
  justify-content: center;
  width: 100px;
  height: 100px;
  border: 10px dotted transparent;
  border-left: 10px dotted #66FFE6;
  border-right: 10px dotted #66FFE6;
  border-bottom: 10px dotted #66FFE6;
  border-top: 10px dotted #66FFE6;
  border-radius: 50%;
  animation: spin 1s linear infinite;
   margin: auto ; 
}

  
  @keyframes spin {
    from {
      transform: rotate(0deg);
    }
  
    to {
      transform: rotate(360deg);
    }
  }